button {
    cursor: pointer;
}

@media (max-width: 768px) {

    #feedback .a,
    #feedback .header,
    #feedback .name,
    #feedback .profile-img,
    #feedback .img1,
    #feedback .img3,
    #feedback .img4,
    #feedback .data,
    #feedback .logs,
    #feedback .faqs,
    #feedback .fs,
    #feedback .billing,
    #feedback .side-header {
        display: none;
    }

    #sideSection {
        display: none;
    }

    #feedback .img2 {
        width: 32px;
        color: #000000;
    }


    #feedback .texting {
        color: #1A2F4E
    }

